import ToastComponent from './toast.vue'    //toast.vue

export default {    // vueinstall
    install: function (Vue) {
        const ToastConstructor = Vue.extend(ToastComponent)    // toast.vueVue
        const instance = new ToastConstructor();    // 

        instance.$mount(document.createElement('div'))    // divbody
        document.body.appendChild(instance.$el)

        // Vue$toastmsgduration
        Vue.prototype.$toast = (msg, type , duration = 2000) => {
            if (instance.visible) return;    // visibletoast.vue

            instance.message = msg;
            instance.visible = true;
            instance.type = type
            setTimeout(() => {    // 1.5s
                instance.visible = false;
            }, duration);
        }
    }
}